import React from 'react';
import '../../App.css';
import { Index } from '../../api/dnd5eapi';
import { ListItem, ListItemText } from '@material-ui/core';

interface IState {
    category: Index;
    select: any;
}

class CategoryElement extends React.Component<any, IState>
{
    constructor(props: any) {
        super(props);
        this.state = { category: props.category, select: props.select };
        this.onSelect = this.onSelect.bind(this);
    }
    render() {
        return (
            <ListItem button onClick={this.onSelect}> <ListItemText primary={this.state.category.name} /></ListItem >
        );
    }

    onSelect() {
        this.state.select(this.state.category);
    }

}

export default CategoryElement;