import React from 'react';
import '../../App.css';
import { Item } from '../../api/dnd5eapi';
import { Collapse, List, ListItem, ListItemIcon, ListItemText, Typography } from '@material-ui/core';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import AddIcon from '@material-ui/icons/Add';
import { classes } from '../../App';


interface IState {
    item: Item;
    click: any;
    open: boolean;
}

class ItemDetails extends React.Component<any, IState>
{
    constructor(props: any) {
        super(props);
        this.state = { item: this.props.item, click: this.props.click, open: false };
        this.onClick = this.onClick.bind(this);
        this.getDescription = this.getDescription.bind(this);
        this.onOpen = this.onOpen.bind(this);
    }
    render() {
        return (
            <div>
                <h1>{this.state.item.name}</h1>
                {this.getDescription()}
            </div>
        );
    }

    getDescription() {
        var item: Item = this.state.item;
        var list = [<ListItem button onClick={this.onClick} ><ListItemIcon><AddIcon /></ListItemIcon> <ListItemText primary={"Add"} /></ListItem>];
        if (item.equipment_category !== undefined && item.equipment_category !== null) {
            list.push(<ListItem ><ListItemText primary={"Equipment Category: " + item.equipment_category?.name} /></ListItem>);
        }
        if (item.gear_category !== undefined && item.gear_category !== null) {
            list.push(<ListItem ><ListItemText primary={"Gear Category: " + item.gear_category?.name} /></ListItem>);
        }
        if (item.quantity !== undefined && item.quantity !== null) {
            list.push(<ListItem ><ListItemText primary={"Quantity: " + item.quantity} /></ListItem>);
        } else {
            list.push(<ListItem ><ListItemText primary={"Quantity: " + 1} /></ListItem>);
        }
        if (item.cost !== undefined && item.cost !== null) {
            list.push(<ListItem ><ListItemText primary={"Cost: " + item.cost?.quantity + " " + item.cost?.unit} /></ListItem>);
        }
        if (item.weight !== undefined && item.weight !== null) {
            list.push(<ListItem ><ListItemText primary={"Weight: " + item.weight} /></ListItem>);
        }
        //var desc: JSX.Element[] = [];
        var desc: string = "";
        if (item.desc !== undefined && item.desc !== null) {
            item.desc.forEach((part) => {
                desc += part + "\n";
            });
            list.push(<ListItem button onClick={this.onOpen}><ListItemIcon>{this.state.open ? <KeyboardArrowDownIcon /> : <KeyboardArrowRightIcon />} </ListItemIcon>{"Description"}</ListItem>);
        }

        return (<List component="nav" className={classes.description_list} >
            {list}
            <Collapse in={this.state.open} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                    <ListItem className={classes.nested}>
                        <Typography variant="body1" component="p">{desc}</Typography>
                    </ListItem>
                </List>
            </Collapse>
        </List>);
    }

    onOpen() {
        this.setState({ open: !this.state.open });
    }

    onClick() {
        this.state.click(this.state.item);
    }

}

export default ItemDetails;