import React from 'react';
import '../../App.css';
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, TextField, Theme, withStyles } from '@material-ui/core';
import { Item } from '../../api/dnd5eapi';

interface IState {
    item: Item;
}

const styles = (theme: Theme) => ({
    root: {
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
});


class CustomItemDialog extends React.Component<any, IState>
{
    classes: any;

    constructor(props: any) {
        super(props);
        console.log(props);
        this.classes = this.props.classes;
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleCostChange = this.handleCostChange.bind(this);
        this.handleCountChange = this.handleCountChange.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleAdd = this.handleAdd.bind(this);

        this.state = {
            item: {
                index: "",
                name: "",
                equipment_category: { index: "", name: "", url: "" },
                gear_category: { index: "", name: "", url: "" },
                quantity: 1,
                cost: {
                    quantity: 0,
                    unit: "gp"
                },
                weight: 0,
                desc: [],
                url: ""
            }
        };
    }

    render() {

        return (
            <Dialog open={this.props.open} onClose={this.handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Add Custom Item</DialogTitle>
                <DialogContent>
                    <DialogContentText>Enter the information about the item below:</DialogContentText>
                    <TextField autoFocus margin="dense" id="name" label="Name" fullWidth onInput={(e: React.ChangeEvent<HTMLInputElement>) => this.handleNameChange(e.target.value)} />
                    <TextField margin="dense" id="cost" label="Cost" type="number" fullWidth onInput={(e: React.ChangeEvent<HTMLInputElement>) => this.handleCostChange(+e.target.value)} />
                    <TextField margin="dense" id="count" label="Count" type="number" fullWidth onInput={(e: React.ChangeEvent<HTMLInputElement>) => this.handleCountChange(+e.target.value)} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleClose} color="primary">Cancel</Button>
                    <Button onClick={this.handleAdd} color="primary">Add Item</Button>
                </DialogActions>
            </Dialog>
        );

    }

    handleNameChange(value: string) {
        this.state.item.index = value;
        this.state.item.name = value;
        this.setState({});
    }
    handleCostChange(value: number) {
        this.state.item.cost.quantity = value;
        this.setState({});
    }
    handleCountChange(value: number) {
        this.state.item.quantity = value;
        this.setState({});
    }

    handleClose() {
        this.props.close();
    }

    handleAdd() {
        this.props.add(this.state.item);
    }



}

export default withStyles(styles, { withTheme: true })(CustomItemDialog);