import React from 'react';
import '../../App.css';
import { ListItem, ListItemText } from '@material-ui/core';

class ItemElement extends React.Component<any>
{
    constructor(props: any) {
        super(props);
        this.onSelect = this.onSelect.bind(this);
    }
    render() {
        return (
            <ListItem button onClick={this.onSelect}><ListItemText primary={this.props.equipment.name} /></ListItem>
        );


    }

    onSelect() {
        this.props.select(this.props.equipment);
    }

}

export default ItemElement;