export class Dnd5eapi {

    public static async getEquipment(name: string): Promise<Item> {
        return await Dnd5eapi.get("/api/equipment/" + name) as Item;
    }

    public static async getMagicCategory(): Promise<EquipmentCategoriesResponse> {
        return await Dnd5eapi.get("/api/magic-items") as EquipmentCategoriesResponse;
    }

    public static async getAllCategory(): Promise<EquipmentCategoriesResponse> {
        return await Dnd5eapi.get("/api/equipment") as EquipmentCategoriesResponse;
    }

    public static async getEquipmentCategory(category: string): Promise<EquipmentCategoryResponse> {
        return await Dnd5eapi.get("/api/equipment-categories/" + category) as EquipmentCategoryResponse;
    }

    public static async getEquipmentCategories(): Promise<EquipmentCategoriesResponse> {
        return await Dnd5eapi.get("/api/equipment-categories") as EquipmentCategoriesResponse;
    }

    public static async getMonsters(rating: number = -1): Promise<EquipmentCategoriesResponse> {
        return await Dnd5eapi.get("/api/monsters" + ((rating >= 0) ? "?challenge_rating=" + rating : "")) as EquipmentCategoriesResponse;
    }

    public static async get(query: string) {
        return await this.sendRequest(query);
    }


    private static async sendRequest(query: string, method: string = "get", body: any = undefined) {
        var response = await fetch("https://www.dnd5eapi.co" + query, { method: method, body: (body === undefined) ? undefined : JSON.stringify(body) });
        var json = await response.json();
        return json;
    }


}


export type Index = {
    index: string;
    name: string;
    url: string;
}


export type EquipmentCategoriesResponse = {
    count: number;
    results: Index[];
}

export type EquipmentCategoryResponse = {
    index: string;
    name: string;
    equipment: Index[];
}


export type Item = {
    index: string;
    name: string;
    equipment_category: Index;
    gear_category: Index;
    quantity: number;
    cost: {
        quantity: number;
        unit: string;
    };
    weight: number;
    desc: string[];
    url: string
}