import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import 'fontsource-roboto';
import LootPickerPage from './pages/loot_picker/loot_picker';
import { Card, CardActions, CardContent, CardHeader, CardMedia, createMuiTheme, createStyles, IconButton, makeStyles, Theme, ThemeProvider, Typography } from '@material-ui/core';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import { red } from '@material-ui/core/colors';

const darkTheme = createMuiTheme({
  palette: {
    type: "dark",
  }
});



export var classes: any;

function App() {
  const useStyles = makeStyles((theme: Theme) =>
    createStyles({
      root: {
      },
      card: {
        height: "425px",
        width: "400px"
      },
      card_grid: {
        height: "90%",
        width: "90%"
      },
      monster_selector_paper: {
        height: "100%",
        width: "100%",
        padding: "10px"
      },
      monster_slider: {
        width: "200px",
        marginRight: "10px"
      },
      monster_selector_grid: {
        height: "350",
        width: "600"
      },
      description_list: {
        height: "60%",
        overflow: 'auto',

      },
      media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
      },
      expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
          duration: theme.transitions.duration.shortest,
        }),
      },
      expandOpen: {
        transform: 'rotate(180deg)',
      },
      avatar: {
        backgroundColor: red[500],
      },
      loot_picker: {
        flexGrow: 1,
        height: "100%",
        maxHeight: "90vh",
        width: "90vw",

      },
      loot_categories_list: {
        height: "80%",
        overflow: 'auto',
        marginLeft: "10px",
        marginRight: "10px",

      },
      loot_items_list: {
        height: "70%",
        overflow: 'auto',
        marginLeft: "10px",
        marginRight: "10px",

      },
      loot_paper: {
        height: "100%",
        maxHeight: "90vh",
        width: "100%",
        paddingTop: "1px",
        overflow: 'hidden',
      },
      loot_table: {
        height: "50%",
        maxHeight: "90vh",
        width: "100%",
        paddingTop: "1px"
      },
      loot_export: {
        height: "45%",
        maxHeight: "90vh",
        width: "100%",
        paddingTop: "5%",
        marginTop: "5%"
      },
      loot_button: {
        margin: "5px"
      },
      control: {
        padding: theme.spacing(2),
      },
    }),
  );

  classes = useStyles();

  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <header className="App-header">
          <Router>

            {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
            <Switch>
              <Route path="/dnd-tools/lootpicker" render={(props) => <LootPickerPage {...props} />} />
              <Route path="/dnd-tools"><Home /> </Route>
            </Switch>
          </Router>
        </header>

      </div>
    </ThemeProvider>

  );
}


function Home() { // CardColumns for more Cards
  return (
    <SelectionCard cardTitle="Loot Picker" cardText="Pick your Loot and create a loot list." cardLink="dnd-tools/lootpicker" />
  );
}

function SelectionCard(props: any) {

  return <Card className={classes.card}>
    <CardHeader
      title={props.cardTitle}
    />
    <CardMedia
      className={classes.media}
      image="https://cdn.dribbble.com/users/191949/screenshots/6235095/image.png"
      title={props.cardTitle}
    />
    <CardContent>
      <Typography variant="body2" color="textSecondary" component="p">
        {props.cardText}
      </Typography>
    </CardContent>
    <CardActions disableSpacing>
      <Link to={"/" + props.cardLink}><IconButton aria-label="open"><KeyboardArrowRightIcon /> </IconButton></Link>
    </CardActions>
  </Card>;

}

export default App;
