import React from 'react';
import '../../App.css';
import { RouteComponentProps } from 'react-router-dom';
import { Dnd5eapi, Index, Item } from '../../api/dnd5eapi';
import CategoryElement from './category_element';
import ItemElement from './item_element';
import ItemDetails from './item_details_element';
import { Button, CircularProgress, Grid, IconButton, List, Paper, TextField } from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import DeleteIcon from '@material-ui/icons/Delete';
import SendIcon from '@material-ui/icons/Send';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import { DataGrid, ColDef, CellParams } from '@material-ui/data-grid';
import { classes } from '../../App';
import { WebHook } from '../../discord/DiscordWebhook';
import { LootImageBuilder } from '../../imagebuilder/ImageBuilder';
import CustomItemDialog from './customitem_dialog';

interface IState {
    loading: Boolean;
    categories: Index[];
    categoryilter: string;
    items: Index[];
    itemFilter: string;
    selected: Item | null;
    loot: Item[];
    webhook: string;
    dialog: boolean;
}
class LootPickerPage extends React.Component<RouteComponentProps<any>, IState>
{
    constructor(props: RouteComponentProps) {
        super(props);
        this.state = { loading: true, categories: [], categoryilter: "", items: [], itemFilter: "", loot: [], selected: null, webhook: "", dialog: false };
        this.loadCategories = this.loadCategories.bind(this);
        this.selectCategory = this.selectCategory.bind(this);
        this.selectCategoryAll = this.selectCategoryAll.bind(this);
        this.selectCategoryMagic = this.selectCategoryMagic.bind(this);
        this.selectItem = this.selectItem.bind(this);
        this.addItemToLoot = this.addItemToLoot.bind(this);
        this.removetemFromLoot = this.removetemFromLoot.bind(this);
        this.setItemSearch = this.setItemSearch.bind(this);
        this.onAdd = this.onAdd.bind(this);
        this.onRemove = this.onRemove.bind(this);
        this.onDelete = this.onDelete.bind(this);
        this.sendWebhook = this.sendWebhook.bind(this);
        this.downloadImage = this.downloadImage.bind(this);

        this.showCustomItemDialog = this.showCustomItemDialog.bind(this);
        this.closeCustomItemDialog = this.closeCustomItemDialog.bind(this);
        this.addCustomItem = this.addCustomItem.bind(this);

    }

    columns: ColDef[] = [
        {
            field: "",
            headerName: "Edit",
            disableClickEventBubbling: true,
            width: 150,
            renderCell: (params: CellParams) => {

                return <div>
                    <IconButton onClick={(e) => this.onAdd(params)} aria-label="add"><AddIcon fontSize="small" /></IconButton>
                    <IconButton onClick={(e) => this.onRemove(params)} aria-label="remove"><RemoveIcon fontSize="small" /></IconButton>
                    <IconButton onClick={(e) => this.onDelete(params)} aria-label="delete"><DeleteIcon fontSize="small" /></IconButton>
                </div>;
            }
        },
        { field: 'id', headerName: 'ID', width: 70, hide: true },
        { field: 'name', headerName: 'Name', width: 100 },
        { field: 'count', headerName: 'Count', width: 90 },
        { field: 'prize', headerName: '$', width: 90 },
    ];

    onAdd(params: CellParams) {
        var index = params.rowIndex;
        if (index !== undefined) {
            var item = this.state.loot[index];
            item.quantity++;
            this.setState({});
        }
    }

    onRemove(params: CellParams) {
        var index = params.rowIndex;
        if (index !== undefined) {
            var item = this.state.loot[index];
            if (item.quantity > 1) {
                item.quantity--;
                this.setState({});
            } else {
                this.onDelete(params);
            }
        }
    }

    onDelete(params: CellParams) {
        var index = params.rowIndex;
        if (index !== undefined) {
            this.state.loot.splice(index, 1);
            this.setState({});
        }
    }

    render() {

        if (this.state.loading) {
            this.loadCategories();
            return <CircularProgress />;
        }

        return (
            <Grid container direction="row" justify="space-evenly" alignItems="center" className={classes.loot_picker} spacing={2}>

                <Grid item xs={12} className={classes.loot_picker}>
                    <Grid container justify="center" spacing={2} className={classes.loot_picker}>
                        <Grid key={1} item xs={2}>
                            <Paper className={classes.loot_paper}>
                                <h2>Categories:</h2>
                                <List component="nav" className={classes.loot_categories_list} >
                                    {this.state.categories.map((value, index) => {
                                        if (value.index === "all") {
                                            return (<CategoryElement index={index} category={value} select={this.selectCategoryAll} />);
                                        } else if (value.index === "magic") {
                                            return (<CategoryElement index={index} category={value} select={this.selectCategoryMagic} />);
                                        } else {
                                            return (<CategoryElement index={index} category={value} select={this.selectCategory} />);
                                        }
                                    })
                                    }
                                </List>
                            </Paper>
                        </Grid>
                        <Grid key={2} item xs={2}>
                            <Paper className={classes.loot_paper}>
                                <h2>Items:</h2>
                                <Button variant="outlined" size="large" onClick={(e) => this.showCustomItemDialog()} startIcon={<AddIcon />}> Add Custom Item </Button>
                                <Autocomplete freeSolo id="free-solo-2-demo" disableClearable options={this.state.items.map((option) => option.name)} renderInput={(params: any) => (
                                    <TextField {...params} label="Filter" margin="normal" variant="outlined" InputProps={{ ...params.InputProps, type: 'search' }} />)}
                                    onChange={(event: any, newValue: string) => {
                                        console.log("change: " + newValue);
                                        this.setItemSearch(newValue);
                                    }}
                                    onInputChange={(event, newInputValue) => {
                                        this.setItemSearch(newInputValue);
                                    }}
                                />
                                <List component="nav" className={classes.loot_items_list} >
                                    {this.state.items.filter(item => item.name.toLowerCase().includes(this.state.itemFilter.toLowerCase())).map((value) => (<ItemElement equipment={value} select={this.selectItem} />))}
                                </List>
                            </Paper>
                        </Grid>
                        <Grid key={3} item xs={3}>
                            <Paper className={classes.loot_paper}> {(this.state.selected !== null) ? <ItemDetails item={this.state.selected} click={this.addItemToLoot} /> : <div></div>} </Paper>
                        </Grid>
                        <Grid key={4} item xs={3}>
                            <Paper className={classes.loot_table}>
                                <DataGrid autoPageSize rows={this.state.loot.map((item: Item, index: number) => ({ id: index, name: item.name, count: item.quantity, prize: (item.quantity * item.cost.quantity) + " " + item.cost.unit }))} columns={this.columns} />
                            </Paper>
                            <Paper className={classes.loot_export}>
                                <h6>Send to Discord:</h6>
                                <TextField id="outlined-basic" label="Webhook-URL" variant="outlined" fullWidth onInput={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ webhook: e.target.value })} />
                                <Button className={classes.loot_button} variant="outlined" size="large" onClick={(e) => this.sendWebhook()} endIcon={<SendIcon />}> Send </Button>
                                <Button className={classes.loot_button} variant="outlined" size="large" onClick={(e) => this.downloadImage()} endIcon={<CloudDownloadIcon />}> Download Image </Button>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
                <CustomItemDialog open={this.state.dialog} close={this.closeCustomItemDialog} add={this.addCustomItem} />
            </Grid>
        );
    }

    showCustomItemDialog() {
        this.setState({ dialog: true });
    }

    closeCustomItemDialog() {
        this.setState({ dialog: false });
    }

    addCustomItem(item: Item) {
        this.addItemToLoot(item);
        this.closeCustomItemDialog();
    }

    async sendWebhook() {
        WebHook.sendMessage("LootTable:", this.state.loot, this.state.webhook);

    }
    async downloadImage() {

        var image = await LootImageBuilder.createImage(this.state.loot);
        //window.open(image);
        this.downloadURI(image, "loot.png");
    }

    downloadURI(uri: string, name: string) {
        var link = document.createElement("a");
        link.download = name;
        link.href = uri;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }

    setItemSearch(value: string) {
        this.setState({ itemFilter: value });
    }

    async loadCategories() {

        var response = await Dnd5eapi.getEquipmentCategories();
        var categories: Index[] = [{ index: "all", name: "All Items", url: "" }, { index: "magic", name: "Magic Items", url: "" }];
        var items = (await Dnd5eapi.getAllCategory()).results;
        this.setState({
            categories: categories.concat(response.results),
            items: items,
            loading: false
        });

    }

    async selectCategoryAll() {
        var response = await Dnd5eapi.getAllCategory();
        this.setState({
            items: []
        });
        this.setState({
            items: response.results
        });
    }

    async selectCategoryMagic() {
        var response = await Dnd5eapi.getMagicCategory();
        this.setState({
            items: []
        });
        this.setState({
            items: response.results
        });
    }

    async selectCategory(category: Index) {
        var response = await Dnd5eapi.getEquipmentCategory(category.index);
        this.setState({
            items: []
        });
        this.setState({
            items: response.equipment
        });
    }

    async selectItem(item: Index) {
        var response = await Dnd5eapi.get(item.url);
        this.setState({
            selected: null
        });
        this.setState({
            selected: response
        });
    }

    addItemToLoot(newItem: Item) {
        if (newItem.quantity === undefined) {
            newItem.quantity = 1;
        }
        if (newItem.cost === undefined) {
            newItem.cost = { quantity: 0, unit: "gp" };
        }

        var found = this.state.loot.find((loot) => loot.index === newItem.index);

        if (found !== undefined) {
            found.quantity = found.quantity + newItem.quantity;
        } else {
            this.state.loot.push({
                index: newItem.index,
                name: newItem.name,
                equipment_category: newItem.equipment_category,
                gear_category: newItem.gear_category,
                quantity: newItem.quantity,
                cost: {
                    quantity: newItem.cost.quantity,
                    unit: newItem.cost.unit
                },
                weight: newItem.weight,
                desc: newItem.desc,
                url: newItem.url,
            });
        }
        this.setState({});
    }

    removetemFromLoot(index: number) {
        var loot = this.state.loot;
        loot.splice(index, 1);
        this.setState({ loot: loot });
    }

}

export default LootPickerPage;