import { createCanvas } from "canvas";
import { CanvasTable, CTConfig, CTData, CTColumn } from "canvas-table";
import { Item } from "../api/dnd5eapi";

export class LootImageBuilder {

    static async createImage(items: Item[]) {

        const canvas = createCanvas(300, 50 + (25 * items.length));
        const data: CTData = [];

        for (var i = 0; i < items.length; i++) {
            var item = items[i];
            data.push([item.name, "" + item.quantity, item.cost.quantity + " " + item.cost.unit]);
        }

        const columns: CTColumn[] = [
            { title: "Item" },
            { title: "Quantity", options: { textAlign: "center" } },
            { title: "Cost", options: { textAlign: "center" } }
        ];
        const config: CTConfig = { columns, data };
        const ct = new CanvasTable(canvas, config);
        await ct.generateTable();

        return canvas.toDataURL();
    }

}